/**
 * üîÑ Venturo v4.0 - Áµ±‰∏ÄÁöÑ Store ÊåÅ‰πÖÂåñÂ∑•ÂÖ∑ÔºàÈáçÊßãÁâàÔºâ
 *
 * ÊîπÁî®Êñ∞ÁöÑÈõ¢Á∑öÊû∂ÊßãÔºö
 * - ‰ΩøÁî® unified-types.ts Áµ±‰∏ÄË≥áÊñôÊ®°Âûã
 * - ‰ΩøÁî® offline-manager.ts ÁÆ°ÁêÜÈõ¢Á∑öË≥áÊñô
 * - Ëá™ÂãïËôïÁêÜ Supabase ÂêåÊ≠•
 */

import { getOfflineManager, StoreName } from '@/lib/offline/offline-manager';
import { generateUUID } from '@/lib/offline/unified-types';

type SetState<T> = (partial: T | Partial<T> | ((state: T) => T | Partial<T>)) => void;
type GetState<T> = () => T;

// Ë°®Ê†ºÂêçÁ®±Êò†Â∞ÑÔºöStore key ‚Üí IndexedDB store name
const TABLE_MAP: Record<string, StoreName> = {
  'tours': 'tours',
  'orders': 'orders',
  'quotes': 'quotes',
  'todos': 'todos',
  'suppliers': 'suppliers',
  'customers': 'customers',
  'payments': 'paymentRequests', // 'payments' Â∞çÊáâÂà∞ 'paymentRequests'
  'paymentRequests': 'paymentRequests',
  'members': 'orders', // members Â≠òÂú® orders Ë£°ÔºàÊö´ÊôÇÔºâ
  'tourAddOns': 'tours', // tourAddOns Â≠òÂú® tours Ë£°ÔºàÊö´ÊôÇÔºâ
};

// ============= CRUD ÊñπÊ≥ïÁîüÊàêÂô®Ôºà‰ΩøÁî®Êñ∞Êû∂ÊßãÔºâ =============

/**
 * Âª∫Á´ãÂÖ∑ÊúâÈõ¢Á∑öÂêåÊ≠•ÂäüËÉΩÁöÑ CRUD ÊñπÊ≥ïÔºàv4.0 ÈáçÊßãÁâàÔºâ
 *
 * @param tableName - Ë°®Ê†ºÂêçÁ®±
 * @param arrayKey - Store ‰∏≠Èô£ÂàóÁöÑ key ÂêçÁ®±ÔºàÂ¶Ç 'todos', 'tours'Ôºâ
 * @param set - Zustand set ÂáΩÊï∏
 * @param get - Zustand get ÂáΩÊï∏
 * @returns CRUD ÊñπÊ≥ïÁâ©‰ª∂
 */
export const createPersistentCrudMethods = <T extends { id?: string }>(
  tableName: string,
  arrayKey: string,
  set: SetState<any>,
  get: GetState<any>
) => {
  const capitalizedName = arrayKey.charAt(0).toUpperCase() + arrayKey.slice(1, -1);
  const storeName = (TABLE_MAP[tableName] || tableName) as StoreName;
  const offlineManager = getOfflineManager();

  return {
    // ===== Êñ∞Â¢û =====
    [`add${capitalizedName}`]: async (data: Omit<T, 'id' | 'createdAt' | 'updatedAt'>) => {
      try {
        // ÁâπÊÆäËôïÁêÜÔºöËá™ÂãïÁîüÊàêÁ∑®Ëôü
        if (tableName === 'quotes' && !(data as any).quoteNumber) {
          const year = new Date().getFullYear();
          const state = get();
          const existingQuotes = state[arrayKey] || [];
          const lastNumber = existingQuotes
            .map((q: any) => q.quoteNumber)
            .filter((num: string) => num?.startsWith(`QUOTE-${year}`))
            .map((num: string) => parseInt(num.split('-')[2] || '0'))
            .sort((a: number, b: number) => b - a)[0] || 0;

          const newNumber = (lastNumber + 1).toString().padStart(4, '0');
          (data as any).quoteNumber = `QUOTE-${year}-${newNumber}`;
        }

        // ‰ΩøÁî® OfflineManager Âª∫Á´ãË≥áÊñô
        const newItem = await offlineManager.create<T>(storeName, data as Partial<T>);

        // Êõ¥Êñ∞ Zustand store
        const state = get();
        set({
          [arrayKey]: [...state[arrayKey], newItem]
        });

        return newItem;
      } catch (error) {
        console.error(`‚ùå Êñ∞Â¢û ${tableName} Â§±Êïó:`, error);
        throw new Error(`ÁÑ°Ê≥ïÊñ∞Â¢û${tableName}: ${error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'}`);
      }
    },

    // ===== Êõ¥Êñ∞ =====
    [`update${capitalizedName}`]: async (id: string, data: Partial<T>) => {
      try {
        // ‰ΩøÁî® OfflineManager Êõ¥Êñ∞Ë≥áÊñô
        const updatedItem = await offlineManager.update<T>(storeName, id, data);

        // Êõ¥Êñ∞ Zustand store
        const state = get();
        const updatedArray = state[arrayKey].map((item: T) =>
          item.id === id ? updatedItem : item
        );
        set({ [arrayKey]: updatedArray });

        return updatedItem;
      } catch (error) {
        console.error(`‚ùå Êõ¥Êñ∞ ${tableName} Â§±Êïó:`, error);
        throw new Error(`ÁÑ°Ê≥ïÊõ¥Êñ∞${tableName}: ${error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'}`);
      }
    },

    // ===== Âà™Èô§ =====
    [`delete${capitalizedName}`]: async (id: string) => {
      try {
        // ‰ΩøÁî® OfflineManager Âà™Èô§Ë≥áÊñô
        await offlineManager.delete(storeName, id);

        // Êõ¥Êñ∞ Zustand store
        const state = get();
        set({
          [arrayKey]: state[arrayKey].filter((item: T) => item.id !== id)
        });

        return true;
      } catch (error) {
        console.error(`‚ùå Âà™Èô§ ${tableName} Â§±Êïó:`, error);
        throw new Error(`ÁÑ°Ê≥ïÂà™Èô§${tableName}: ${error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'}`);
      }
    },

    // ===== ËºâÂÖ• =====
    [`load${capitalizedName}s`]: async () => {
      try {
        // ‰ΩøÁî® OfflineManager ËºâÂÖ•Ë≥áÊñô
        const data = await offlineManager.getAll<T>(storeName);

        // Êõ¥Êñ∞ Zustand store
        set({ [arrayKey]: data });

        console.log(`‚úÖ ËºâÂÖ• ${tableName}:`, data.length, 'Á≠Ü');
        return data;
      } catch (error) {
        console.error(`‚ö†Ô∏è ËºâÂÖ• ${tableName} Â§±Êïó:`, error);
        return null;
      }
    }
  };
};

// ============= ËºîÂä©ÂáΩÊï∏ =============

/**
 * ÁîüÊàêÂîØ‰∏Ä IDÔºà‰ΩøÁî® unified-types ÁöÑ UUID ÁîüÊàêÂô®Ôºâ
 */
export const generateId = (): string => {
  return generateUUID();
};

/**
 * ÁîüÊàêÊôÇÈñìÊà≥
 */
export const generateTimestamp = (): string => {
  return new Date().toISOString();
};

/**
 * ÈåØË™§ËôïÁêÜÂåÖË£ùÂô®
 */
export const withErrorHandling = <T>(
  operation: () => T,
  errorMessage: string
): T => {
  try {
    return operation();
  } catch (error) {
    console.error(`‚ùå ${errorMessage}:`, error);
    throw new Error(`${errorMessage}: ${error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'}`);
  }
};
