'use client';

import React, { useState, useRef, useMemo, useCallback } from 'react';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useAccountingStore } from '@/stores/accounting-store';
import {
  Wallet,
  CreditCard,
  Plus,
  TrendingUp,
  TrendingDown,
  DollarSign,
  PieChart,
  ArrowUpDown,
  Calendar,
  BarChart3,
  Settings,
  Zap,
  UtensilsCrossed,
  Car,
  ShoppingBag,
  Gamepad2,
  Package,
  Edit3,
  MoreHorizontal
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { AccountsOverview } from '@/components/accounting/accounts-overview';
import { TransactionList } from '@/components/accounting/transaction-list';
import { AccountsManagement } from '@/components/accounting/accounts-management';
import { AddAccountDialog } from '@/components/accounting/add-account-dialog';

export default function AccountingPage() {
  const { accounts, stats, categories, transactions, addTransaction } = useAccountingStore();
  const [activeTab, setActiveTab] = useState<'overview' | 'transactions' | 'accounts' | 'settings'>('overview');
  const [isAddAccountOpen, setIsAddAccountOpen] = useState(false);
  const [theme, setTheme] = useState<'morandi' | 'moneypro'>('morandi');

  // Âø´ÈÄüË®òÂ∏≥ÁãÄÊÖã
  const [quickAmount, setQuickAmount] = useState('');
  const [quickCategory, setQuickCategory] = useState('4'); // È†êË®≠È§êË≤ª
  const [quickAccount, setQuickAccount] = useState(accounts.length > 0 ? accounts[0].id : '');
  const [showToast, setShowToast] = useState(false);
  const amountInputRef = useRef<HTMLInputElement>(null);
  const categorySelectRef = useRef<HTMLButtonElement>(null);
  const addButtonRef = useRef<HTMLButtonElement>(null);

  const tabs = [
    { id: 'overview', label: 'Á∏ΩË¶Ω', icon: BarChart3 },
    { id: 'transactions', label: '‰∫§Êòì', icon: ArrowUpDown },
    { id: 'accounts', label: 'Â∏≥Êà∂', icon: Wallet },
    { id: 'settings', label: 'Ë®≠ÂÆö', icon: Settings },
  ];

  // Â∏∏Áî®ÂàÜÈ°ûÈÖçÁΩÆ
  const quickCategories = [
    { id: '4', name: 'È§êË≤ª', icon: 'Food', color: '#F97316' },
    { id: '5', name: '‰∫§ÈÄö', icon: 'Transport', color: '#8B5CF6' },
    { id: '6', name: 'Ë≥ºÁâ©', icon: 'Shopping', color: '#EC4899' },
    { id: '7', name: 'Â®õÊ®Ç', icon: 'Game', color: '#6366F1' },
    { id: '8', name: 'ÂÖ∂‰ªñ', icon: 'Other', color: '#6B7280' },
  ];

  // ‰ªäÊó•‰∫§Êòì
  const today = new Date().toISOString().split('T')[0];
  const todayTransactions = useMemo(() =>
    transactions
      .filter(t => t.date === today)
      .sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime())
      .slice(0, 3)
  , [transactions, today]);

  // Âø´ÈÄüË®òÂ∏≥ËôïÁêÜ
  const handleQuickTransaction = useCallback(async () => {
    if (!quickAmount || !quickCategory || !quickAccount) return;

    const categoryData = categories.find(c => c.id === quickCategory);
    const accountData = accounts.find(a => a.id === quickAccount);

    if (!categoryData || !accountData) return;

    const transactionData = {
      accountId: quickAccount,
      accountName: accountData.name,
      categoryId: quickCategory,
      categoryName: categoryData.name,
      type: 'expense' as const,
      amount: parseFloat(quickAmount),
      currency: 'TWD',
      description: '',
      date: today,
    };

    addTransaction(transactionData);

    // Ê∏ÖÁ©∫Ë°®ÂñÆ‰∏¶È°ØÁ§∫ÊàêÂäüÂãïÁï´
    setQuickAmount('');
    setShowToast(true);
    setTimeout(() => setShowToast(false), 2000);

    // ÈáçÊñ∞ËÅöÁÑ¶Âà∞ÈáëÈ°çËº∏ÂÖ•
    setTimeout(() => {
      amountInputRef.current?.focus();
    }, 100);
  }, [quickAmount, quickCategory, quickAccount, categories, accounts, today, addTransaction]);

  // ÂàÜÈ°ûÂø´ÈÄüÈÅ∏Êìá
  const handleQuickCategorySelect = useCallback((categoryId: string) => {
    setQuickCategory(categoryId);
    setTimeout(() => {
      addButtonRef.current?.focus();
    }, 100);
  }, []);

  // ÈáëÈ°çËº∏ÂÖ•ÂÆåÊàê
  const handleAmountComplete = useCallback(() => {
    if (quickAmount) {
      categorySelectRef.current?.focus();
    }
  }, [quickAmount]);

  // Ë®àÁÆóÊú¨ÊúàËàá‰∏äÊúàÂ∑ÆÁï∞
  const currentMonth = new Date().getMonth();
  const currentYear = new Date().getFullYear();
  const lastMonth = currentMonth === 0 ? 11 : currentMonth - 1;
  const lastMonthYear = currentMonth === 0 ? currentYear - 1 : currentYear;

  const lastMonthExpense = useMemo(() =>
    transactions
      .filter(t => {
        const transactionDate = new Date(t.date);
        return t.type === 'expense' &&
               transactionDate.getMonth() === lastMonth &&
               transactionDate.getFullYear() === lastMonthYear;
      })
      .reduce((sum, t) => sum + t.amount, 0)
  , [transactions, lastMonth, lastMonthYear]);

  const expenseDifference = lastMonthExpense - stats.monthlyExpense;
  const daysToEndOfMonth = new Date(currentYear, currentMonth + 1, 0).getDate() - new Date().getDate();

  // ‰∏ªÈ°åÈÖçÁΩÆ
  const themeConfig = {
    morandi: {
      bg: 'bg-gradient-to-br from-[#F8F5F0] to-[#E6DDD4]',
      headerBg: 'bg-gradient-to-r from-[#6B5B73] to-[#B5986A]',
      cardBg: 'bg-gradient-to-br from-white to-[#F8F5F0]',
      primary: 'text-[#6B5B73]',
      secondary: 'text-[#6B5B73]/60',
      accent: 'bg-gradient-to-r from-[#B5986A] to-[#D4C4A8]',
      border: 'border-[#E6DDD4]'
    },
    moneypro: {
      bg: 'bg-gradient-to-b from-slate-700 to-slate-800',
      headerBg: 'bg-gradient-to-r from-slate-800 to-slate-700',
      cardBg: 'bg-slate-600',
      primary: 'text-white',
      secondary: 'text-slate-300',
      accent: 'bg-gradient-to-r from-blue-600 to-blue-700',
      border: 'border-slate-500'
    }
  };

  const currentTheme = themeConfig[theme];

  return (
    <div className={cn("min-h-screen pb-20", currentTheme.bg)}>
      {/* APPÈ¢®Ê†ºÈ†ÇÈÉ®ÂçÄÂüü */}
      <div className={cn("text-white p-4 sm:p-6 mx-4 mt-4 rounded-3xl shadow-lg", currentTheme.headerBg)}>
        <div className="flex justify-between items-center mb-4">
          <h1 className="text-xl sm:text-2xl font-bold">Ë®òÂ∏≥Âä©Êâã</h1>
          <div className="flex items-center space-x-2">
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setTheme(theme === 'morandi' ? 'moneypro' : 'morandi')}
              className="text-white hover:bg-white/20 rounded-full p-2 text-xs"
            >
              {theme === 'morandi' ? 'Dark' : 'Light'}
            </Button>
          </div>
        </div>

        {/* Êú¨ÊúàÊîØÂá∫‰∏ªÂç°Áâá */}
        <div className="bg-white/15 backdrop-blur-sm rounded-2xl p-4 sm:p-6 mb-4">
          <div className="text-center">
            <div className="text-white/80 text-sm mb-1">Êú¨ÊúàÊîØÂá∫</div>
            <div className="text-3xl sm:text-4xl font-bold text-white mb-2">
              NT$ {stats.monthlyExpense.toLocaleString()}
            </div>
            <div className="flex flex-col sm:flex-row items-center justify-center space-y-2 sm:space-y-0 sm:space-x-4 text-sm text-white/80">
              <div className="flex items-center space-x-1">
                <TrendingUp size={14} />
                <span>Êî∂ÂÖ•: NT$ {stats.monthlyIncome.toLocaleString()}</span>
              </div>
              {expenseDifference > 0 && (
                <div className="flex items-center space-x-1 text-green-300">
                  <span>ÊØî‰∏äÊúàÁúÅ‰∫Ü NT$ {expenseDifference.toLocaleString()}</span>
                </div>
              )}
            </div>
            <div className="text-xs text-white/60 mt-2">
              Ë∑ùÈõ¢ÊúàÂ∫ïÈÇÑÊúâ {daysToEndOfMonth} Â§©
            </div>
          </div>
        </div>
      </div>

      <div className="px-4 mt-4 space-y-6">
        {/* Âø´ÈÄüË®òÂ∏≥ÂçÄÂüü */}
        <div className={cn("rounded-2xl p-6 shadow-lg border", currentTheme.cardBg, currentTheme.border)}>
          <div className="flex items-center space-x-2 mb-6">
            <Zap className={cn("w-5 h-5", theme === 'morandi' ? 'text-[#B5986A]' : 'text-blue-400')} />
            <h2 className={cn("text-lg font-semibold", currentTheme.primary)}>Âø´ÈÄüË®òÂ∏≥</h2>
          </div>

          {/* ‰∏âÊåâÈàïÂçÄÂüü - ÈüøÊáâÂºèË®≠Ë®à */}
          <div className="space-y-4 sm:space-y-0 sm:grid sm:grid-cols-3 sm:gap-3 mb-6">
            {/* ÈáëÈ°çËº∏ÂÖ• */}
            <div className="sm:col-span-1">
              <div className={cn("rounded-xl border-2 p-4 h-20 transition-colors",
                theme === 'morandi' ? 'bg-white border-[#E6DDD4] focus-within:border-[#B5986A]' : 'bg-slate-700 border-slate-500 focus-within:border-blue-400'
              )}>
                <div className={cn("text-xs mb-1", currentTheme.secondary)}>ÈáëÈ°ç</div>
                <input
                  ref={amountInputRef}
                  type="number"
                  value={quickAmount}
                  onChange={(e) => setQuickAmount(e.target.value)}
                  onKeyDown={(e) => e.key === 'Enter' && handleAmountComplete()}
                  placeholder="0"
                  className={cn("w-full text-xl font-bold bg-transparent border-0 focus:outline-none",
                    currentTheme.primary,
                    theme === 'morandi' ? 'placeholder-[#6B5B73]/30' : 'placeholder-slate-400'
                  )}
                />
              </div>
            </div>

            {/* ÂàÜÈ°ûÈÅ∏Êìá */}
            <div className="sm:col-span-1">
              <Select value={quickCategory} onValueChange={setQuickCategory}>
                <SelectTrigger
                  ref={categorySelectRef}
                  className={cn("h-20 border-2 rounded-xl text-center",
                    theme === 'morandi'
                      ? 'bg-white border-[#E6DDD4] text-[#6B5B73] focus:border-[#B5986A]'
                      : 'bg-slate-700 border-slate-500 text-white focus:border-blue-400'
                  )}
                >
                  <SelectValue>
                    <div className="flex items-center justify-center space-x-2">
                      <span className="font-semibold">
                        {quickCategories.find(c => c.id === quickCategory)?.name || 'È§êË≤ª'}
                      </span>
                    </div>
                  </SelectValue>
                </SelectTrigger>
                <SelectContent>
                  {quickCategories.map((category) => (
                    <SelectItem key={category.id} value={category.id}>
                      <span>{category.name}</span>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {/* Ë®òÂ∏≥ÊåâÈàï */}
            <div className="sm:col-span-1">
              <Button
                ref={addButtonRef}
                onClick={handleQuickTransaction}
                disabled={!quickAmount || !quickCategory}
                className={cn("w-full h-20 text-white font-semibold rounded-xl border-0 shadow-lg transition-all duration-200 active:scale-95 disabled:opacity-50 disabled:active:scale-100 text-base",
                  currentTheme.accent
                )}
              >
                <Plus size={20} className="mr-2" />
                Ë®òÂ∏≥
              </Button>
            </div>
          </div>

          {/* Â∏∏Áî®ÂàÜÈ°ûÂø´ÈÄüÈÅ∏Êìá */}
          <div className="space-y-3">
            <div className={cn("text-sm", currentTheme.secondary)}>Â∏∏Áî®ÂàÜÈ°û</div>
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-5 gap-2">
              {quickCategories.map((category) => (
                <button
                  key={category.id}
                  onClick={() => handleQuickCategorySelect(category.id)}
                  className={cn(
                    "flex items-center justify-center px-3 py-3 sm:px-4 rounded-full transition-all duration-200 active:scale-95 text-sm sm:text-base",
                    quickCategory === category.id
                      ? (theme === 'morandi' ? "bg-[#B5986A] text-white shadow-lg" : "bg-blue-600 text-white shadow-lg")
                      : (theme === 'morandi' ? "bg-white/80 text-[#6B5B73] hover:bg-white border border-[#E6DDD4]" : "bg-slate-700 text-slate-300 hover:bg-slate-600 border border-slate-500")
                  )}
                >
                  <span className="font-medium">{category.name}</span>
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* ‰ªäÊó•‰∫§ÊòìÁ∞°ÂåñÂàóË°® */}
        <div className={cn("rounded-2xl p-6 shadow-lg border", currentTheme.cardBg, currentTheme.border)}>
          <div className="flex items-center justify-between mb-4">
            <h3 className={cn("text-lg font-semibold", currentTheme.primary)}>‰ªäÊó•‰∫§Êòì</h3>
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setActiveTab('transactions')}
              className={cn("rounded-full text-sm",
                theme === 'morandi' ? 'text-[#B5986A] hover:bg-[#B5986A]/10' : 'text-blue-400 hover:bg-blue-400/10'
              )}
            >
              Êü•ÁúãÂÖ®ÈÉ®
            </Button>
          </div>

          {todayTransactions.length > 0 ? (
            <div className="space-y-3">
              {todayTransactions.map((transaction) => {
                const category = quickCategories.find(c => c.id === transaction.categoryId);
                return (
                  <div key={transaction.id} className={cn("flex items-center justify-between p-3 sm:p-4 rounded-xl border",
                    theme === 'morandi' ? 'bg-white/60 border-[#E6DDD4]/50' : 'bg-slate-700 border-slate-600'
                  )}>
                    <div className="flex items-center space-x-3 flex-1 min-w-0">
                      <div className="min-w-0 flex-1">
                        <div className={cn("font-medium truncate", currentTheme.primary)}>{transaction.categoryName}</div>
                        <div className={cn("text-sm", currentTheme.secondary)}>
                          {new Date(transaction.createdAt).toLocaleTimeString('zh-TW', {
                            hour: '2-digit',
                            minute: '2-digit'
                          })}
                        </div>
                      </div>
                    </div>
                    <div className="flex items-center space-x-2 flex-shrink-0">
                      <div className="text-right">
                        <span className={cn(
                          "font-bold text-base sm:text-lg",
                          transaction.type === 'expense'
                            ? (theme === 'morandi' ? "text-[#D2691E]" : "text-red-400")
                            : (theme === 'morandi' ? "text-[#228B22]" : "text-green-400")
                        )}>
                          {transaction.type === 'expense' ? '-' : '+'}NT$ {transaction.amount.toLocaleString()}
                        </span>
                      </div>
                      <Button
                        variant="ghost"
                        size="sm"
                        className={cn("p-1 sm:p-2",
                          theme === 'morandi' ? 'text-[#6B5B73]/40 hover:text-[#6B5B73]' : 'text-slate-400 hover:text-slate-300'
                        )}
                      >
                        <Edit3 size={14} />
                      </Button>
                    </div>
                  </div>
                );
              })}
            </div>
          ) : (
            <div className={cn("text-center py-8", currentTheme.secondary)}>
              <Package size={40} className={cn("mx-auto mb-3 sm:w-12 sm:h-12",
                theme === 'morandi' ? 'text-[#B5986A]/40' : 'text-blue-400/40'
              )} />
              <p className="text-base sm:text-lg">‰ªäÊó•ÈÇÑÊ≤íÊúâ‰∫§ÊòìÁ¥ÄÈåÑ</p>
              <p className="text-sm">‰ΩøÁî®‰∏äÊñπÂø´ÈÄüË®òÂ∏≥ÈñãÂßãÂêß</p>
            </div>
          )}
        </div>

        {/* ÂÖ∂‰ªñÂàÜÈ†ÅÂÖßÂÆπ */}
        {activeTab !== 'overview' && (
          <div className={cn("rounded-2xl shadow-lg border", currentTheme.cardBg, currentTheme.border)}>
            {activeTab === 'transactions' && (
              <div className="p-6">
                <TransactionList />
              </div>
            )}

            {activeTab === 'accounts' && (
              <div className="p-6">
                <AccountsManagement />
              </div>
            )}

            {activeTab === 'settings' && (
              <div className="p-6">
                <div className="text-center py-8">
                  <Settings size={48} className="mx-auto mb-3 text-[#B5986A]" />
                  <h3 className="text-lg font-semibold text-[#6B5B73] mb-2">Ë®≠ÂÆöÂäüËÉΩ</h3>
                  <p className="text-[#6B5B73]/60">Âç≥Â∞áÊé®Âá∫Êõ¥Â§öË®≠ÂÆöÈÅ∏È†Ö</p>
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      {/* APPÈ¢®Ê†ºÂ∫ïÈÉ®Â∞éËà™ */}
      <div className={cn("fixed bottom-0 left-0 right-0 backdrop-blur-lg border-t px-2 sm:px-4 py-2 sm:py-3 safe-area-bottom",
        theme === 'morandi'
          ? 'bg-gradient-to-r from-white/95 to-[#F8F5F0]/95 border-[#E6DDD4]/50'
          : 'bg-slate-800/95 border-slate-600'
      )}>
        <div className="flex items-center justify-around max-w-lg mx-auto">
          {tabs.map((tab) => {
            const Icon = tab.icon;
            const isActive = activeTab === tab.id;
            return (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id as any)}
                className={cn(
                  'flex flex-col items-center space-y-1 p-2 sm:p-3 rounded-xl transition-all duration-200 min-w-0 flex-1',
                  isActive
                    ? (theme === 'morandi' ? 'text-[#B5986A] bg-[#B5986A]/10 scale-110' : 'text-blue-400 bg-blue-400/10 scale-110')
                    : (theme === 'morandi' ? 'text-[#6B5B73]/60 hover:text-[#6B5B73] hover:bg-[#6B5B73]/5' : 'text-slate-400 hover:text-slate-300 hover:bg-slate-700')
                )}
              >
                <Icon size={20} className="sm:w-6 sm:h-6" />
                <span className="text-xs font-medium truncate">{tab.label}</span>
              </button>
            );
          })}
        </div>
      </div>


      {/* ÊàêÂäüÊèêÁ§∫Toast */}
      {showToast && (
        <div className="fixed top-4 left-1/2 transform -translate-x-1/2 z-50">
          <div className="bg-gradient-to-r from-green-500 to-green-600 text-white px-6 py-3 rounded-full shadow-lg flex items-center space-x-2 animate-pulse">
            <div className="w-2 h-2 bg-white rounded-full animate-ping" />
            <span className="font-medium">Ë®òÂ∏≥ÊàêÂäüÔºÅ</span>
          </div>
        </div>
      )}


      {/* Êñ∞Â¢ûÂ∏≥Êà∂Â∞çË©±Ê°Ü */}
      <AddAccountDialog
        isOpen={isAddAccountOpen}
        onClose={() => setIsAddAccountOpen(false)}
      />
    </div>
  );
}